import React, { useEffect, Suspense, lazy } from 'react'
import { BrowserRouter as Router, Redirect, Route, Switch } from 'react-router-dom'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import BigNumber from 'bignumber.js'
import { useFetchPublicData } from 'state/hooks'
import { ResetCSS } from '@pancakeswap-libs/uikit'
import GlobalStyle from './style/Global'
import Menu from './components/Menu'
import PageLoader from './components/PageLoader'

// Route-based code splitting
const NotFound = lazy(() => import('./views/NotFound'))
const Marketplace = lazy(() => import('./views/Marketplace'))
const NftMarketplace = lazy(() => import('./views/NftMarketplace'))
const VLADCollection = lazy(() => import('./views/VLADCollection'))
const VLADCollectionDetail = lazy(() => import('./views/VLADCollectionDetail'))
const MyNFT = lazy(() => import('./views/MyNFT'))

// This config is required for number formating
BigNumber.config({
  EXPONENTIAL_AT: 1000,
  DECIMAL_PLACES: 80,
})

const App: React.FC = () => {
  const { account, connect } = useWallet()
  useEffect(() => {
    if (!account && window.localStorage.getItem('accountStatus')) {
      connect('injected')
    }
  }, [account, connect])

  useFetchPublicData()

  return (
    <Router>
      <Menu>
        <Suspense fallback={<PageLoader />}>
          <Switch>
          <Route path="/" exact component={Marketplace} />
          <Route path="/vlad-collection" exact component={VLADCollection} />
          <Route path="/vlad-detail/:id" exact component={VLADCollectionDetail} />
          <Route path="/my-nft">
            {!account ? <Redirect to="/" /> : <MyNFT />}
          </Route>
          <Route component={NotFound} />
          </Switch>
        </Suspense>
      </Menu>
    </Router>
  )
}

export default React.memo(App)
